.classroom {
  .live-container {
    position: relative;

    .video-container {
      position: absolute;
      margin-left: 10px;
      top: 10px;
      left: 67px;
      display: flex;
      z-index: 5;
    }

    .left-offset {
      left: 67px;
    }
  }
}

.media-board {
  overflow: hidden;
  height: 100%;
}

.interactive {
  display: flex;
  justify-content: flex-end;
  position: absolute;
  z-index: 3;
  &.student {
    bottom: 10px;
    right: 10px;
  }
  .items {
    display: flex;
    height: 42px;
    width: 42px;
    border-radius: 4px;
    box-sizing: border-box;
    &.hands_up::after {
      width: 100%;
      height: 100%;
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
      content: ' ';
      background-image: url('../../assets/icon-hand@2x.png');
    }
    &.hands_up_end::after {
      width: 100%;
      height: 100%;
      display: block;
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
      content: ' ';
      background-image: url('../../assets/icon-hand-disable.png');
    }
  }
}